
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'


const wx = require('weixin-js-sdk')

var wxReady = false;

//TODO 抽取上传图片服务
//上传图片到微信服务器
var serverIds = [];
var choosePictureIds = [];
var currentUploadPictureIndex = 0;
function uploadChoosePictures(){
  var choosePictureId = choosePictureIds[currentUploadPictureIndex];
  alert("before update = " + choosePictureId);
  wx.uploadImage({
      localId: choosePictureId, // 需要上传的图片的本地ID，由chooseImage接口获得
      isShowProgressTips: 1, // 默认为1，显示进度提示
      success: function (res) {
          alert("update success = " + res.serverId);
          serverIds.push(res.serverId);
          currentUploadPictureIndex++;
          if(currentUploadPictureIndex >= choosePictureIds.length){
            currentUploadPictureIndex = 0;
            savePicture();
            alert("update done");
          }else{
            uploadChoosePictures();
            alert("update continue");
          }
      }
  });
}

//TODO refactor
//保存微信服务器图片ID到要提交的变量
var pictureUsage = "";
function savePicture(){
  if("banner" == pictureUsage){
    bannerPictureWxServerId = serverIds[0];
    uploadBannerLabel = '已上传封面图片';
  }

  if("auction" == pictureUsage){
    auctionPictureWxServerId = serverIds[0];
    uploadAuctionLabel = '已上传竞拍大厅图片';
  }

  if("show" == pictureUsage){
    showPicturesWxServerId.concat(serverIds);
    uploadShowLabel = '已上传' + showPicturesWxServerId.length + '张商品详情页顶部图片';
  }

  if("desc" == pictureUsage){
    descPicturesWxServerId.concat(serverIds);
    uploadDescLabel = '已上传' + descPicturesWxServerId.length + '张上传商品详情页详情图片';
  }
}

export default{
    components: {
      Box,
      XInput,
      XButton,
      Divider,
      Flexbox,
      FlexboxItem,
      Datetime,
      Confirm,
      Loading,
      Alert
    },
    mounted:function(){
      this.$http.get('/wx/jssdk/config').then(
      	function(response){
      		var data = response.body;
      		var config = {
      			appId : data.appId,
      			timestamp : data.timestamp,
      			nonceStr : data.nonceStr,
      			signature : data.signature,
      			jsApiList : ["chooseImage","uploadImage"]
      		};
      		wx.config(config);
      		wx.ready(function(){
      			wxReady = true;
      		})
      		wx.error(function(res){
      			alert("error info:"+res)
      		})
      	}
      	,
      	function(){
      		console.log("error")
      	});
    },
    data(){
        return{
            bannerPictureWxServerId:'',
            title:'拍品标题',
            startingPrice:0,
            bidIncrement:0,
            cashDeposit:0,
            delayCycle:0,
            startTime:'',
            endTime:'',
            auctionPictureWxServerId:'',
            showPicturesWxServerId:[],
            descPicturesWxServerId:[],
            uploadBannerLabel:'请上传封面图片',
            uploadAuctionLabel:'请上传竞拍大厅图片',
            uploadShowLabel:'请上传商品详情页顶部图片',
            uploadDescLabel:'请上传商品详情页详情图片',
            showSubmitConfirm:false,
            showSubmitLoading:false,
            showSubmitSuccessTip:false,
            showSubmitFailTip:false
        }
    },
    methods:{
      uploadPicture(count,pictureUsage){
        if(!wxReady){
          alert("还没准备好")
          return
        }
        this.pictureUsage = pictureUsage;
        wx.chooseImage({
            count: count, // 默认9
            //sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
            //sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
            success: function (res) {
                alert("choose file = " + res.localIds.toString());
                choosePictureIds = res.localIds; // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
                uploadChoosePictures();
            }
        });
      },
      setNow (timeType) {
        //TODO refactor
        if('start' == timeType){
          this.startTime = dateFormat(new Date(), 'YYYY-MM-DD HH:mm:ss');
        }
        if('end' == timeType){
          this.endTime = dateFormat(new Date(), 'YYYY-MM-DD HH:mm:ss');
        }
      },
      submitGoods(){
        this.showSubmitLoading = true;
        var newGoods = {
          bannerPictureWxServerId : this.bannerPictureWxServerId,
          title : this.title,
          startingPrice : this.startingPrice,
          bidIncrement : this.bidIncrement,
          cashDeposit:this.cashDeposit,
          delayCycle:this.delayCycle,
          startTime:this.startTime,
          endTime:this.endTime,
          auctionPictureWxServerId:this.auctionPictureWxServerId,
          showPicturesWxServerId:this.showPicturesWxServerId,
          descPicturesWxServerId:this.descPicturesWxServerId
        }
        this.$http.post("/goods",newGoods).then(
          function(response){
            this.showSubmitLoading = false;
            this.showSubmitSuccessTip = true;
          },
          function(){
            this.showSubmitLoading = false;
            this.showSubmitFailTip = true;
          }
        )
      },
      backToHome(){
        window.history.back();
      }
    }
}



